import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
HabukhanCashDiscount.propTypes = {
  discount: PropTypes.object,
};
export default function HabukhanCashDiscount({ discount }) {
  const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
    mtn: Yup.string().required(),
    airtel: Yup.string().required(),
    glo: Yup.string().required(),
    mobile: Yup.string().required(),

    mtn_number: Yup.string().required(),
    airtel_number: Yup.string().required(),
    glo_number: Yup.string().required(),
    mobile_number: Yup.string().required(),
  });

  const defaultValues = useMemo(
    () => ({
      mtn: discount?.mtn,
      airtel: discount?.airtel,
      glo: discount?.glo,
      mobile: discount?.mobile,

      mtn_number: discount?.mtn_number,
      airtel_number: discount?.airtel_number,
      glo_number: discount?.glo_number,
      mobile_number: discount?.mobile_number,
    }),
    [discount]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount !== undefined) {
      reset(defaultValues);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/cash/discount/charges/account/${AccessToken}/system/secure`, data)
      swal('success', `Airtime 2 cash Edited`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField name="mtn" label="MTN" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="airtel" label="AIRTEL" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="glo" label="GLO" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="mobile" label="9MOBILE" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />

              <RHFTextField name="mtn_number" label="MTN Number" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="airtel_number" label="AIRTEL Number" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="glo_number" label="GLO Number" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
              <RHFTextField name="mobile_number" label="9MOBILE Number" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }} />
            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
